	module.exports = {"pathContext":{"posts":[{"html":"<h2>Redis Commands 缩写</h2>\n<p>Redis里存储的value是有类型的，不同类型的值需要用不同的command来操作：</p>\n<ul>\n<li>List -> L<Cmd>, e.g., LPUSH, LLEN</li>\n<li>Hash -> H<Cmd>, e.g., HGET, HKEYS</li>\n<li>Set -> S<Cmd>, e.g, SADD, SISMEMBER,</li>\n<li>Sorted Set -> Z<Cmd></li>\n</ul>\n<p>其他缩写：</p>\n<ul>\n<li>M<Cmd> , Multiple</li>\n<li>R<Cmd>, Remove or Right(tail)</li>\n<li>B<Cmd>, Blocking, for instance, BLPOP (pop from head), BRPOP (pop from tail)</li>\n</ul>","id":"/volume1/homes/leo/github/my-blog-code/src/pages/2022/Redis Commands.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-09-21T20:08:16.000+08:00","path":"/2022/redis-commands","title":"Redis Commands 缩写","excerpt":"","tags":["redis"]}}],"tagName":"redis"}}

